#app{min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;background-color:#eeeeee;background-color:var(--background-primary);color:#282c34;color:var(--text-primary);font-family:'Source Sans Pro'}:root{--background-primary: #eeeeee;--text-primary: #282c34}.no-select{pointer-events:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}input[type='number']{-moz-appearance:textfield}.ui-layer{display:flex;position:absolute;top:0;right:0;bottom:0;left:0;z-index:2000000000;pointer-events:none}.ui-layer>*{pointer-events:auto}button.ui.dribble-btn{background-color:rgba(30,30,30,0.75);border-radius:0;width:33px;height:33px}

body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}pre,code,kbd,samp{font-family:SFMono-Regular, Menlo, Monaco, Consolas, 'Liberation Mono', 'Courier New', monospace;font-size:1em}code{font-size:87.5%;color:#e83e8c;word-wrap:break-word}a>code{color:inherit}kbd{padding:0.15rem 0.5rem;font-family:SFMono-Regular, Menlo, Monaco, Consolas, 'Liberation Mono', 'Courier New', monospace;font-size:1em;font-size:87.5%;color:#fff;background-color:#212529;border-radius:0.2rem}

